.button-orange {
	display: table;
	border-radius: 50px;
	background: $primary-color;
	color: #fff;
	padding: 5px 20px;
	font-size: 20px;
	font-family: 'Montserrat';
	i {
		margin-left: 10px; }
	&:hover {
		color: #fff; } }

.button-white {
	border: none;
	display: table;
	border-radius: 50px;
	color: #352e2c;
	background: #fff;
	padding: 5px 20px;
	font-size: 20px;
	font-family: 'Montserrat';
	i {
		margin-left: 10px;
		color: $primary-color; }
	&._shadow {
		box-shadow: 0 5px 25px rgba(#2d2d2d, .1); }

	&:hover {
		color: $primary-color; } }



