.checkout-breadscrumb {
	margin-top: 80px;
	margin-bottom: 55px; }
.checkout-form {
	.form-group {
		margin-bottom: 35px; } }

.checkout-title {
	margin-bottom: 45px;
	h3 {
		font-family: "Montserrat";
		font-weight: bold;
		color: #2d2d2d;
		font-size: 24px; }
	p {
		color: $primary-color;
		font-weight: 500; } }

.checkout-change-address {
	margin-bottom: 25px;
	p {
		color: #252525;
		font-weight: 500;
		font-size: 20px; } }

.checkout-title._detail-extend {
	margin-bottom: 30px; }
.checkout-bill-box {
	margin-bottom: 20px;
	display: flex;
	flex-wrap: wrap;
	border: 1px solid #e1e1e1;
	padding-top: 20px;
	border-radius: 10px;
	p {
		font-size: 14px;
		font-weight: 500;
		color: #2d2d2d;
		padding: 0 30px;
		border-left: 1px solid #e1e1e1;
		&:first-child {
			border-left: 0; }
		span {
			font-weight: 400;
			color: #adadad; }
		&.last {
			width: 100%;
			border-left: 0; } } }

.checkout-p-end {
	font-size: 14px;
	font-weight: 400;
	color: #adadad; }
.coupon-check {
	height: 52px;
	border-radius: 30px;
	background: $primary-color;
	color: #fff;

	display: flex;
	align-items: center;
	position: relative;
	padding-left: 20px;
	font-size: 14px;
	font-weight: bold;
	span {
		display: block;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #fff;
		color: $primary-color;
		font-weight: bold;
		margin-right: 5px; }
	.x {
		position: absolute;
		top: 50%;
		right: 20px;
		color: #fff;
		transform: translateY(-50%);
		cursor: pointer;
		transition: all .3s ease-in-out;
		&:hover {
			color: black; } }

	&-btn {
		height: 43px;
		border: none;
		color: #fff;
		background: $primary-color;
		font-weight: bold;
		border: 1px solid $primary-color;
		width: 100%;
		text-align: center;
		border-radius: 30px;
		transition: all .3s ease-in-out;
		font-size: 14px;
		&:hover {
			color: $primary-color;
			background: none; } } }

.checkout-done-btn {
	height: 43px;
	background: $primary-color;
	border: 2px solid $primary-color;
	font-weight: bold;
	color: #fff;
	padding: 0 20px;
	border-radius: 30px;
	transition: all .3s ease-in-out;
	width: 100%;
	&:hover {
		color: $primary-color;
		background: none; } }

.cart-table-checkout-title {
	margin-top: 80px;
	margin-bottom: 20px; }
.cart-table {
	border-left: 1px solid #e1e1e1;
	border-right: 1px solid #e1e1e1;
	.table {
		margin-bottom: 0;
		.thead-light {
			th {
				padding: 25px 45px;
				background: $primary-color;
				color: #fff;
				border-top: none;
				border-bottom: none; } }

		td {
			font-size: 14px;
			color: #adadad;
			padding: 25px 45px;
			border-top: 0;
			a {
				color: #adadad;
				&:hover {
					color: $primary-color; } } }
		tbody tr:nth-of-type(even) {
			background: #f8f8f8; }

		tbody tr {
			// border-left: 1px solid #e1e1e1
			// td:last-child
			// 	border-right: 1px solid #e1e1e1
 } } }			// 	margin-right: -1px
.cart-end {
	padding-left: 45px;
	padding-right: 45px;
	padding-top: 35px;
	padding-bottom: 30px;
	border: 1px solid #e1e1e1;
	p {
		display: flex;
		justify-content: space-between;
		color: #adadad;
		font-size: 14px;

		&:last-child {
			margin-bottom: 0; }
		span:first-child {
			color: #2d2d2d;
			font-weight: 500; }

		.primary-color {
			color: $primary-color; } } }

.checkout-form._detail-extend {
	padding-bottom: 90px; }
.cart-table._detail-extend {
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	.table {
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
		overflow: hidden;
		.thead-light {
			th {
				padding-left: 25px;
				padding-right: 25px;
				vertical-align: middle; } }
		th, td {
			font-size: 14px; }
		td {
			padding-left: 25px;
			padding-right: 25px; } } }
.cart-end._detail-extend {
	padding-left: 25px;
	padding-right: 25px;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px; }


.customer-info-text {
	padding-top: 30px;
	h5 {
		font-size: 25px;
		font-weight: 500;
		color: #2d2d2d;
		margin-bottom: 20px; }
	.spacer {
		height: 20px;
		width: 100%; }
	p {
		font-size: 14px;
		font-weight: normal;
		color: #2d2d2d;
		span {
			color: #adadad; } }

	.btn-end {
		height: 30px;
		display: table;
		line-height: 30px;
		padding: 0 15px;
		border-radius: 20px;
		color: #fff;
		background: $primary-color;
		font-size: 14px;
		font-weight: 500; } }
@media (max-width: 991.82px) {
	.cart-table._responsive {
		width: 100%;
		overflow: auto;
		.table {
			min-width: 690px; } } }


@media (max-width: 575.82px) {

	.checkout-breadscrumb {
		margin: 40px 0; } }







