@import 'elements/header';
@import 'elements/footer';
@import 'elements/news';
@import 'elements/contact-block';


.page-function {
	position: fixed;
	top: 20%;
	left: 0;
	z-index: 2;
	width: 100%; }
.page-function-wrap {
	position: relative;
	height: .1px;
	overflow: visible;
	.list {
		position: absolute;
		left: -15px;
		transform: translate(-100%); }
	.func {
		margin-bottom: 10px;
		position: relative;
		.icon {
			width: 45px;
			height: 45px;
			border-radius: 50%;
			box-shadow: {}
			position: relative;
			box-shadow: 0 0 20px rgba(#2d2d2d, .2);
			display: flex;
			align-items: center;
			justify-content: center;
			color: $primary-color;
			background: #fff;
			font-size: 20px;
			position: relative;
			z-index: 2;
			transition: all .3s ease-in-out; }

		.hidden-p {
			position: absolute;
			top: 0;
			left: 0;
			height: 45px;
			background: #fff;
			padding-left: 45px;
			border-radius: 25px;
			box-shadow: 0 0 20px rgba(#2d2d2d, .2);
			padding-right: 20px;
			opacity: 0;
			transition: all .3s ease-in-out;
			pointer-events: none;
			transform: translateX(-10px);
			.f-nav {
				white-space: nowrap;
				> a, > .link-title {
					line-height: 45px;
					color: #2d2d2d;
					font-size: 16px; } }

			input {
				border: none;
				border-bottom: 1px solid rgba(black, .3);
				height: 35px;
				padding-top: 8px; } }
		.f-nav {
			position: relative;
			ul {
				position: absolute;
				top: 100%;
				left: 5px;
				padding-top: 10px;
				>li:first-child a {
					border-top-left-radius: 3px;
					border-top-right-radius: 3px; }
				>li:last-child a {
					border-bottom-left-radius: 3px;
					border-bottom-right-radius: 3px; }
				>li > a {
					font-size: 12px;
					font-weight: normal;
					color: #2d2d2d;
					display: block;
					position: relative;
					padding: 5px 10px;
					padding-right: 20px;
					background: #fff;
					transition: all .3s ease-in-out;
					i {
						position: absolute;
						top: 50%;
						left: 5px;
						transform: translateY(-50%);
						opacity: 0;
						transition: all .3s ease-in-out; }

					&:hover {
						color: $primary-color;
						padding-left: 15px;
						padding-right: 15px;
						i {
							opacity: 1; } } } } }
		&:hover, &.active {
			.icon {
				box-shadow: none; }

			.hidden-p {
				opacity: 1;
				pointer-events: auto;
				transform: translateX(0px); } } } }


@media (max-width: 991.82px) {
	.page-function-wrap .list {
		left: 0; } }

@media (max-width: 767.82px) {
	.page-function {
		top: 65px; }
	.page-function-wrap {
		.list {
			left: 100%; }

		.func {
			.hidden-p {
				right: 0;
				left: auto;

				padding-left: 20px;
				padding-right: 30px;
				height: 30px;

				.f-nav {
					> a, > .link-title {
						line-height: 30px;
						font-size: 14px; }
					ul {
						left: auto;
						right: 5px; } }
				input {
					height: 25px; } }
			.icon {
				width: 30px;
				height: 30px;
				font-size: 15px; } } } }




