.ham {
	width: 20px;
	height: 20px;
	position: relative;
	cursor: pointer;
	div {
		position: absolute;
		left: 50%;
		width: 20px;
		height: 2px;
		background: $primary-color;
		top: 50%;
		transform: translate(-50%, -50%);
		&:before, &:after {
			width: 100%;
			height: 2px;
			background: $primary-color;
			position: absolute;
			content: '';
			top: -7px; }
		&:after {
			top: auto;
			bottom: -7px; } } }


.page-header {
	position: relative; }

.page-header-content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	.logo {
		width: 125px;
		img {
			width: 100%; } }
	.nav {
		display: flex;
		align-items: center;
		ul {
			display: flex;
			font-family: "Roboto";
			a {
				margin-right: 35px;
				display: block;
				font-size: 18px;
				font-weight: 300;
				color: #2d2d2d;
				padding: 20px 0;
				position: relative;
				transition: all .2s ease-in-out;
				&:before {
					position: absolute;
					content: '';
					bottom: 0;
					left: 0;
					width: 100%;
					height: 2px;
					background: $primary-color;
					opacity: 0;
					transition: all .2s ease-in-out; } }
			> li.active, li:hover {
				a {
					color: $primary-color; }
				a:before {
					opacity: 1; } } }

		.auth {
			margin-right: 10px;
			&._logged i {
				color: $primary-color; } }

		.shopping-cart {
			position: relative;
			a:hover {
				color: #fff; }
			.num {
				position: absolute;
				top: 0;
				right: 0;
				transform: translateY(-10px);
				background: $primary-color;
				color: #fff;
				border: 30px;
				text-align: center;
				width: 20px;
				height: 20px;
				line-height: 20px;
				border-radius: 20px;
				font-size: 12px; }
			.menu-cart {
				left: auto!important;
				width: 375px;
				transform: none!important;
				right: 0;
				top: 40px!important;
				padding: 0;
				border-radius: 10px;
				overflow: hidden;
				border: none;
				box-shadow: 0 5px 30px rgba(black, .15);
				.cart-head {
					background: #f7f7f7;
					p {
						margin: 0;
						padding: 20px;
						font-size: 18px;
						font-family: 'Montserrat';
						font-weight: bold;
						span {
							color: $primary-color; } } }
				.cart-body-item {
					padding: 10px 25px;
					border-top: 1px solid #e6e6e6;
					display: flex;
					align-items: center;
					position: relative;
					&:last-child {
						border-bottom: 1px solid #e6e6e6; }

					.img {
						width: 70px;
						position: relative;
						.fixed-thumb {
							padding-bottom: 100%; }
						.num {
							right: -5px;
							top: 3px; } }

					.text {
						padding-left: 20px;
						p {
							margin: 0;
							font-size: 12px;
							font-weight: bold; } }
					.x {
						width: 10px;
						height: 10px;
						position: absolute;
						right: 25px;
						top: 15px;

						&:before, &:after {
							width: 100%;
							height: 1px;
							background: #bbbbbb;
							content: '';
							position: absolute;
							top: 50%;
							left: 50%;
							transform: translate(-50%, 50%) rotate(45deg); }
						&:after {
							transform: translate(-50%, 50%) rotate(-45deg); }
						&:hover {
							&:before, &:after {
								background: $primary-color; } } } }
				.cart-footer {
					a {
						display: block;
						text-align: center;
						color: #fff;
						background: $primary-color;
						padding: 18px 0;
						font-size: 16px;
						font-weight: bold;
						font-family: "Roboto";
						letter-spacing: 1px; } } } }
		.ham {
			margin-left: 15px; } } }

.page-header-content {
	.mobile-menu {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		z-index: 10;
		background: #fff;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		overflow: hidden;
		box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
		ul {
			> li {
				> a {
					color: $primary-color;
					display: block;
					padding: 10px 20px;
					border-top: 1px solid rgba($primary-color, .3);
					transition: all .2s ease-in-out;
					&:hover {
						background: $primary-color;
						color: #fff; } } } } } }
.header-btn-style {
	height: 30px;
	line-height: 30px;
	padding: 0 15px;
	color: #fff;
	display: table;
	border-radius: 35px;
	background: #352e2c;
	font-size: 14px; }


.auth-modal {
	.modal-dialog {
		max-width: 377px; }
	.modal-header {
		background: #f7f7f7; }
	.modal-content {
		border: none;
		border-radius: 10px;
		overflow: hidden; }
	.modal-title {
		font-size: 18px;
		font-weight: bold;
		text-transform: uppercase;
		font-family: "Montserrat";
		padding-left: 50px;
		padding-right: 50px; }
	.close {
		margin-right: 30px; }

	.modal-body {
		padding: 50px;
		padding-bottom: 30px;
		.order-link {
			display: flex;
			justify-content: space-between;
			margin-top: 13px;
			a {
				color: "Roboto";
				font-weight: 500;
				color: #2d2d2d;
				font-size: 12px;
				text-decoration: underline;
				&._2 {
					color: $primary-color;
					text-decoration: none; } } } }

	input {
		height: 60px;
		font-family: "Roboto";
		font-weight: 500;
		font-size: 12px;
		border: 1px solid #dddddd;
		width: 100%;
		margin-bottom: -1px;
		padding-left: 25px;
		padding-right: 25px;
		&:first-child {
			border-top-left-radius: 10px;
			border-top-right-radius: 10px; }
		&:last-child {
			border-bottom-left-radius: 10px;
			border-bottom-right-radius: 10px; } }


	.modal-footer {
		text-align: center;
		padding: 0;
		button {
			flex-grow: 1;
			display: block;
			text-align: center;
			background: $primary-color;
			color: #fff;
			border: none;
			font-family: "Roboto";
			font-weight: bold;
			font-size: 16px;
			letter-spacing: 1px;
			padding: 20px 10px; } } }


@media (max-width: 1199.82px) {
	.page-header-content {
		padding: 15px 0; } }


@media (max-width: 575.82px) {
	.page-header-content {
		.logo {
			width: 100px; }
		.nav {
			.shopping-cart {
				.t {
					display: none; }

				a {
					display: flex;
					justify-content: center;
					align-items: center;
					padding: 7px; }
				.num {
					right: -9px; } } } } }



