.shop-page-banner {
	background: $primary-color;
	color: #fff;
	&:before {
		position: absolute;
		content: '';
		bottom: 0;
		left: 0;
		width: 100%;
		height: 115px;
		background: #fff; }
	.head-text {
		display: flex;
		align-items: center;
		padding-top: 45px;
		padding-bottom: 25px;
		> * {
			flex-shrink: 0;
			max-width: 100%; } }
	.head-left-text {
		padding-right: 30px;
		margin-right: 30px;
		border-right: 1px solid rgba(#fff, .2);
		h5 {
			font-weight: 500;
			font-size: 30px;
			letter-spacing: 2px; }

		p {
			font-size: 18px;
			margin-bottom: 0; } }

	.head-right-text {
		p {
			font-size: 16px;
			margin-bottom: 0;
			font-weight: 300;
			span {
				font-weight: 500; } } } }

.shop-page-info {
	display: flex;
	box-shadow: 0 0 25px rgba(#2d2d2d, .2);
	position: relative;
	.logo {
		background: #fff;
		width: 295px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 30px;
		img {
			height: auto; } }

	.desc {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		padding: 65px;
		background-size: cover;
		background-position: bottom left;
		h4 {
			font-size: 30px;
			color: #2d2d2d;
			font-weight: normal;
			font-family: 'Playfair Display SC', serif; }
		h5 {
			font-size: 20px;
			color: #2d2d2d;
			font-weight: 100; }
		hr {
			width: 53px;
			border-bottom: 1px solid rgba(#2d2d2d, .5);
			margin-left: 0; }
		p {
			font-size: 12px;
			font-weight: 300;
			color: #2d2d2d; } } }


.shop-hot-section {
	padding-top: 40px;
	padding-bottom: 30px;
	.title-2__ {
		margin-bottom: 40px;
		h4 {
			text-align: center;
			margin: 0; } } }

.product-card._shop-hot-ext {
	.fixed-thumb {
		padding-bottom: 100%; }

	@media (min-width: 1199.82px) {
		.text {
			h5 {
				font-size: 18px; } }
		.end {
			span {
				font-size: 20px; }
			a {
				font-size: 14px;
				font-weight: 500;
				height: 30px;
				line-height: 28px; } } } }

.shop-hot-swiper-wrapper {
	position: relative; }

.shop-hot-navigation {
	position: absolute;
	z-index: 1;
	pointer-events: none;
	top: 35%;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: space-between;
	> * {
		background: $primary-color;
		pointer-events: auto;
		cursor: pointer;
		padding: 10px 15px;
		border-radius: 30px;
		&:focus {
			outline: none; } }
	.prev {
		transform: translateX(-15px); }
	.next {
		transform: translateX(15px);
		img {
			transform: scale(-1, 1); } } }



.shop-news-section {
	background: #f8f8f8;
	padding-top: 40px;
	padding-bottom: 40px;
	.title__ {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		margin-bottom: 30px;
		hr {
			margin-left: 0;
			margin-bottom: 0; } } }

@media (max-width: 991.82px) {

	.shop-page-banner {
		.head-text {
			flex-wrap: wrap; }
		.head-left-text {
			width: 100%;
			border-right: 0;
			margin-bottom: 20px; } } }


@media (max-width: 767.82px) {
	.shop-page-info .desc h4 {
		font-size: 25px; } }


@media (max-width: 575.82px) {
	.shop-page-info {
		flex-direction: column;
		.logo {
			width: 100%; }

		.desc {
			padding: 15px; } } }



