.shop-register-section {
	text-align: center;
	padding-top: 85px;
	padding-bottom: 90px;
	h5 {
		font-size: 20px;
		font-weight: normal;
		color: #352e2c;
		font-family: 'Montserrat';
		margin-bottom: 0; }
	h3 {
		font-size: 30px;
		font-weight: 600;
		color: $primary-color;
		font-family: "Montserrat";
		margin-bottom: 35px; }

	p {
		font-size: 16px;
		color: #adadad;
		font-weight: 300;
		font-family: "Montserrat"; }
	hr {
		margin-bottom: 0;
		margin-top: 35px; } }


.shop-advantage-section {
	background: #f8f8f8; }

.shop-advantage-text-wrap {
	padding-top: 85px;
	padding-bottom: 55px; }
.shop-advantage-text {
	display: flex;
	align-items: flex-start;
	margin-bottom: 25px;
	padding-right: 50px;
	&:last-child {
		margin-bottom: 0; }
	.num {
		flex-shrink: 0;
		font-family: "Montserrat";
		font-size: 50px;
		font-weight: bold;
		color: $primary-color;
		opacity: .2;
		width: 100px;
		line-height: 1; }
	.p {
		flex-grow: 1;
		h5 {
			font-size: 20px;
			font-weight: bold;
			font-family: "Roboto";
			color: #2d2d2d;
			margin-bottom: 15px; }
		p {
			font-size: 16px;
			font-weight: 300;
			color: #adadad; } } }

.shopRegisterForm-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: bottom left; }
.contact-info-fshop {
	position: relative;
	z-index: 1;
	padding-left: 50px;
	padding-right: 25px;
	padding-top: 50px;
	padding-bottom: 40px;
	.spacer {
		height: 35px;
		width: 100%; }
	h5 {
		font-size: 20px;
		font-weight: bold;
		color: #fff;
		font-family: 'Montserrat'; }
	p {
		font-family: "Montserrat";
		font-size: 16px;
		color: #fff;
		letter-spacing: 1px;
		margin-bottom: 0;
		&.last {
			font-weight: 500; } }

	hr {
		border-color: rgba(#fff, .3);
		border-bottom-width: 1px;
		width: 100%;
		margin: 0; }

	form {
		margin-top: 20px;
		button {
			margin-top: 35px;
			font-weight: normal;
			font-size: 18px; } } }



@media (max-width: 575.82px) {
	.shop-register-section {
		padding-top: 40px;
		padding-bottom: 40px;
		p {
			text-align: justify; }

		h3 {
			font-size: 25px; } }

	.shop-advantage-text-wrap {
		padding-top: 40px;
		padding-bottom: 40px; }
	.shop-advantage-text {
		flex-direction: column;
		padding-right: 0;
		.p p {
			text-align: justify; } }
	.shopRegisterForm-bg {
		width: 100vw!important;
		left: 50%;
		transform: translateX(-50%); }

	.contact-info-fshop {
		padding-left: 0;
		padding-right: 0; } }



