.page-news-head {
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid #e1e1e1;
	padding-bottom: 20px;
	padding-top: 40px;
	align-items: flex-end;
	.left {
		p {
			font-size: 14px;
			color: #252525;
			font-weight: 500;
			margin-bottom: 0; } }

	.right {
		ul {
			display: flex;
			> li {
				margin-left: 20px;
				> a {
					font-size: 18px;
					color: #adadad;
					font-weight: 400; }
				&.active > a {
					font-weight: 500;
					color: #252525; } } } } }
.news-section {
	padding-top: 45px;
	.load-more {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 80px;
		hr {
			width: 300px;
			border-color: #e1e1e1;
			border-bottom-width: 1px;
			margin-bottom: 20px;
			margin-top: 0; }
		a {
			font-weight: bold;
			font-size: 14px;
			color: $primary-color;
			letter-spacing: 2px;
			display: block; } } }
.news-card {
	display: flex;
	flex-wrap: wrap;
	> * {
		width: 50%;
		flex-shrink: 0; }

	.text {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 70px;
		padding-top: 20px;
		padding-bottom: 20px;
		.tag {
			color: $primary-color;
			font-size: 18px;
			font-weight: bold;
			margin-bottom: 10px; }
		h3 {
			font-size: 30px;
			color: #252525;
			font-weight: bold; }
		.time {
			margin-bottom: 25px;
			font-size: 14px;
			color: #adadad;
			font-weight: 500;
			span {
				margin-right: 10px; } }

		p {
			margin-bottom: 25px;
			font-size: 14px;
			color: #252525;
			font-weight: 400; }

		a {
			display: table;
			font-size: 14px;
			font-weight: 500;
			color: #fff;
			background: $primary-color;
			align-self: flex-start;
			height: 30px;
			border: 1px solid $primary-color;
			line-height: 29px;
			padding: 0 20px;
			border-radius: 20px;
			transition: all .3s ease-in-out;
			&:hover {
				color: $primary-color;
				background: none; } } }
	.img {
		.fixed-thumb {
			padding-bottom: 80.34%; } } }
.news-card._reverse {
	.text {
		order: {}
		padding-left: 35px;
		padding-right: 50px; }
	.img {
		order: 2; } }


@media (max-width: 991.82px) {
	.news-card {
		margin-bottom: 40px;
		> * {
			width: 100%; }
		&._reverse {
			.img {
				order: 1; }
			.text {
				order: 2;
				padding-left: 0;
				padding-right: 0; } }

		.img {
			.fixed-thumb {
				padding-bottom: 50%; } }

		.text {
			padding-left: 0;
			padding-right: 0; } } }

@media (max-width: 767.82px) {
	.page-news-head {
		flex-direction: column;
		align-items: flex-start;
		.left {
			margin-bottom: 10px; }

		.right ul>li {
			margin-left: 0;
			margin-right: 10px; } } }

@media (max-width: 575.82px) {

	.news-card .text h3 {
		font-size: 20px; }

	.page-news-head {

		.right ul {
			flex-wrap: wrap;
			>li {
				width: auto;
				margin-right: 15px;
				a {
					white-space: nowrap;
					font-size: 15px; } } } }

	.news-section .load-more {
		margin-top: 30px; } }



