.page-footer {
	background: #352e2c;
	.content {
		display: flex;
		justify-content: space-between;
		font-family: "Roboto";
		align-items: center;
		padding-top: 10px;
		padding-bottom: 15px;
		ul {
			display: flex;
			li {
				margin: 0 15px; }
			a {
				color: #fff;
				font-size: 18px;
				font-weight: 300;
				color: #adadad;
				display: block; } }
		.copy-right {
			font-size: 18px;
			font-weight: normal;
			color: #fff; } }


	@media (max-width: 991.82px) {
		.content {
			padding-top: 15px;
			flex-direction: column;
			.logo {
				margin-bottom: 20px; }
			ul {
				margin-bottom: 20px; } } }

	@media (max-width: 575.82px) {
		.content {
			ul {
				flex-wrap: wrap;
				li {
					width: 50%;
					margin: 0; }
				a {
					text-align: center; } } } } }



