@import 'components/fixedThumb';
@import 'components/buttons';
@import 'components/title';
@import 'components/carts';
@import 'components/form-components';
@import 'components/banner';

.bg-cover {
	background-size: cover;
	background-position: center; }

hr.section-divider {
	width: 100%;
	border-bottom-width: 1px;
	border-bottom-color: #e1e1e1; }


.form-control-1 {
	height: 58px;
	background: #f5f5f5;
	color: #666666;
	border: none;
	border-radius: 10px;
	font-size: 15px;
	font-family: 'Montserrat';
	font-weight: 300;
	width: 100%;
	padding: 0 20px; }


.breadscrumb {
	p {
		font-size: 14px;
		color: #2d2d2d;
		font-weight: 600;
		a {
			color: #2d2d2d;
			&:hover {
				color: $primary-color; } }
		span.di {
			color: #adadad;
			margin: 0 5px; } } }


.pag-1 {
	display: flex;
	align-items: center;
	a, span {
		color: #2d2d2d;
		font-weight: 500;
		font-size: 18px;
		margin-right: 10px; }
	li.active > a {
		color: $primary-color; }
	li > a:hover {
		color: $primary-color; } }


.list-w-orange-dot {
	> li {
		position: relative;
		padding-left: 10px;
		&:before {
			width: 3px;
			height: 3px;
			background: $primary-color;
			border-radius: 3px;
			top: 9;
			left: 0;
			position: absolute;
			content: ''; }
		> a {
			display: block;
			font-size: 14px;
			color: #898989; } } }




