.product-relate-section {
	background: #f8f8f8;
	padding-top: 50px;
	padding-bottom: 50px;
	.title-2__ {
		margin-bottom: 35px;
		h4 {
			text-align: center;
			margin: 0; } } }

.product-row-list {
	> * {
		display: flex; } }
.product-card {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
	.fixed-thumb {
		padding-bottom: 120.37%; }
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(black, .5);
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
