		h5 {
			font-size: 16px;
			font-weight: 500;
			color: #252525; }
		a:hover > h5 {
			color: $primary-color; } }

	.end {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		span {
			font-size: 15px;
			color: #252525;
			font-weight: 400; }
		.price {
			flex-grow: 1;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			span:first-child {
				margin-right: 10px; }
			span:first-child + span {
				text-decoration: line-through; } } }

	a.buy-now {
		height: 25px;
		line-height: 23px;
		border: 1px solid $primary-color;
		background: $primary-color;
		color: #fff;
		font-size: 12px;
		padding: 0 10px;
		font-weight: 500;
		border-radius: 15px;
		transition: all .3s ease-in-out;
		white-space: nowrap;
		&:hover {
			background: none;
			color: $primary-color; } } }
.product-swiper {
	.swiper-slide {
		display: flex; }
	.product-card {
		display: flex;
		justify-content: flex-start;
		flex-direction: column; } }

