.title__ {
	h3 {
		font-size: 35px;
		font-family: 'Montserrat';
		font-weight: normal;
		color: #352e2c;
		letter-spacing: 2px;
		span {
			font-weight: 600; }

		@media (max-width: 575.82px) {
			font-size: 25px; } } }

hr {
	border: none;
	border-bottom: 5px solid $primary-color;
	width: 50px; }


.title-2__ {
	h4 {
		font-size: 25px;
		font-weight: bold;
		color: #2d2d2d;
		font-family: "Montserrat"; }

	@media (max-width: 575.82px) {
		h4 {
			font-size: 20px; } } }



