.subpage-banner {

	height: 320px;
	position: relative;
	background-position: center;
	background-size: cover;
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(black, .5); }
	.text {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translateY(-50%);
		z-index: 1;
		h3 {
			text-align: center;
			color: #fff;
			font-size: 40px;
			font-weight: bold; } }

	@media (max-width: 575.82px) {
		.text {
			h3 {
				font-size: 25px; } } } }



.subpage-banner._bg-color-only {
	background: $primary-color!important;
	.overlay {
		opacity: 0; }
	hr {
		border-color: #fff; }

	@media (max-width: 767.82px) {
		height: 200px; } }




