.form-control-2 {
	height: 43px;
	width: 100%;
	border-radius: 30px;
	font-size: 14px;
	color: #4e4e4e;
	border: 1px solid #c2c2c2;
	background: none;
	padding: 0 15px;
	&:focus {
		border-color: $primary-color; } }

textarea.form-control-2 {
	height: auto;
	border-radius: 10px;
	padding-top: 10px;
	padding-bottom: 10px; }


label.label-2 {
	font-size: 14px;
	color: #2d2d2d;
	font-weight: 500;
	sup {
		color: $primary-color;
		font-weight: bold; }

	&._font-normal {
		color: #adadad;
		font-weight: normal; } }

.nice-select-1 .nice-select {
	float: none;
	height: 43px;
	border-color: #c2c2c2;
	border-radius: 30px;
	line-height: 42px;
	&.open {
		border-color: $primary-color; }
	&:after {
		right: 17px; }

	.list {
		width: 100%; }
	.option:hover, .option.focus, .option.selected.focus {
		background: rgba($primary-color, .1); } }


.form-control-3 {
	height: 45px;
	border-radius: 10px;
	background: #fff;
	border: none;
	padding: 0 15px;
	font-size: 14px;
	width: 100%; }


.range-slider-input {
	.ui-widget.ui-widget-content {
		background: none;
		border: none;
		&:before {
			position: absolute;
			content: '';
			top: 3px;
			left: 0;
			width: 100%;
			height: 3px;
			background: #d7d7d7; } }

	.ui-slider-horizontal .ui-slider-range {
		background: none;
		&:before {
			position: absolute;
			content: '';
			top: 3px;
			left: 0;
			width: 100%;
			height: 3px;
			background: $primary-color; } }

	.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default, .ui-button, html .ui-button.ui-state-disabled:hover, html .ui-button.ui-state-disabled:active {
		background: none;
		border: none;
		&:before {
			position: absolute;
			content: '';
			width: 15px;
			height: 15px;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background: $primary-color;
			border-radius: 50%; }
		&:focus {
			outline: none; } } }


.range-slider-input {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	.rangeSlider {
		width: 100%; }

	.startVal, .endVal {
		margin-top: 10px;
		font-size: 14px;
		color: #191919; } }





