.career-banner {
	padding-top: 20px;
	padding-bottom: 20px;
	background-size: cover;
	background-position: bottom left;
	h2 {
		font-size: 40px;
		color: #fff; }

	ul {
		> li {
			position: relative;
			padding-left: 15px;
			&:before {
				position: absolute;
				content: '';
				top: 7px;
				left: 0px;
				width: 10px;
				height: 10px;
				border-radius: 10px;
				background: #fff; }
			> a {
				font-size: 18px;
				color: #fff; } } }

	hr {
		border-bottom: 1px solid rgba(white, .2);
		width: 600px;
		margin-left: 0; }

	p {
		font-size: 13px;
		color: rgba(white,1);
		font-weight: 300;
		margin-bottom: 0;

		&._last {
			font-weight: 500;
			font-size: 13px;
			span {
				margin-left: 15px;
				white-space: nowrap; } } } }

.career-section {
	width: 100%;
	overflow: hidden; }

.career-text-wrap {
	padding-top: 85px; }
.career-text {
	margin-bottom: 30px;
	max-width: 700px;
	h3 {
		font-size: 30px;
		font-weight: 600;
		color: #2d2d2d;
		font-family: "Montserrat";
		margin-bottom: 15px; }

	p {
		font-size: 17px;
		color: #adadad;
		font-weight: 300;
		font-family: "Montserrat"; } }

.career-note {
	font-size: 20px;
	color: $primary-color;
	max-width: 440px;
	font-weight: 600;
	font-family: "Montserrat"; }
hr.c {
	width: calc(100% - 30px);
	margin-left: 0;
	margin-top: 50px;
	border-bottom: 1px solid #e1e1e1; }


.career-contact-info {
	background: $primary-color;
	color: #fff;
	position: relative;
	padding: 30px 50px;
	padding-right: 0;
	&:before {
		width: 100vw;
		height: 100%;
		top: 0;
		left: 0;
		position: absolute;
		content: '';
		background: $primary-color; }
	> * {
		position: relative;
		z-index: 1; }
	h5 {
		font-size: 20px;
		font-weight: bold;
		color: #fff;
		font-family: "Montserrat"; }

	p {
		margin-bottom: 0;
		font-size: 16px;
		font-weight: 300;
		font-family: "Montserrat"; } }

.career-news {
	padding-bottom: 20px;
	position: relative;
	padding-left: 50px;
	padding-top: 35px;
	&:before {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100%;
		content: '';
		background: #f8f8f8; }

	.title {
		position: relative;
		font-size: 25px;
		color: #2d2d2d;
		font-weight: 600;
		font-family: "Montserrat";
		margin-bottom: 20px; } }
.career-news-item {
	position: relative;
	margin-bottom: 30px;
	.fixed-thumb {
		padding-bottom: 28.57%; }

	.text {
		margin-top: 10px;
		h6 {
			font-size: 20px;
			color: #352e2c;
			font-weight: 500; } }

	.time {
		font-size: 14px;
		color: #adadad; }

	a.more {
		font-size: 12px;
		color: #f15927;
		font-weight: 500;
		i {
			margin-left: 5px; } } }


@media (max-width: 767.82px) {
	.career-banner h2 {
		font-size: 30px; }
	.career-text-wrap {
		padding-top: 30px; }

	.career-contact-info {
		padding-left: 0;
		&:before {
			width: 120vw;
			left: 50%;
			transform: translateX(-50%); } }


	.career-news {
		padding-left: 0;
		&:before {
			width: 120vw;
			left: 50%;
			transform: translateX(-50%); } } }


@media (max-width: 575.82px) {
	.career-text {
		h3 {
			font-size: 25px; }
		p {
			font-size: 15px; } } }





