.main-banner {
	.fixed-thumb {
		padding-bottom: 41.53%;
		z-index: 1; }
	&:before {
		z-index: 2;
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(255,255,255,1);
		background: -moz-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(246,246,246,0.53) 47%, rgba(237,237,237,0) 100%);
		background: -webkit-gradient(left top, right top, color-stop(0%, rgba(255,255,255,1)), color-stop(47%, rgba(246,246,246,0.53)), color-stop(100%, rgba(237,237,237,0)));
		background: -webkit-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(246,246,246,0.53) 47%, rgba(237,237,237,0) 100%);
		background: -o-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(246,246,246,0.53) 47%, rgba(237,237,237,0) 100%);
		background: -ms-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(246,246,246,0.53) 47%, rgba(237,237,237,0) 100%);
		background: linear-gradient(to right, rgba(255,255,255,1) 0%, rgba(246,246,246,0.53) 47%, rgba(237,237,237,0) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ededed', GradientType=1 ); }


	.text {
		z-index: 3;
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		width: 100%;
		font-family: 'Roboto';
		h5 {
			font-size: 20px;
			font-weight: 100;
			color: $primary-color;
			margin: 0; }
		h3 {
			font-size: 40px;
			font-weight: bold;
			color: #000;
			margin: 0; }
		p {
			font-size: 20px;
			font-weight: 300;
			margin: 0; }
		.button-orange {
			margin-top: 33px; } } }


.banner-base-func {
	position: absolute;
	bottom: 20px;
	left: 0;
	width: 100%;
	z-index: 1;
	.content {
		display: flex;
		flex-wrap {}
		justify-content: space-between;
		background: #352e2c;
		align-items: center;
		padding: 0px 30px;
		border-radius: 50px;
		font-family: Roboto;
		> * {
			width: calc(100% / 3); }
		.cate {
			position: relative;
			color: #fff;
			font-size: 18px;
			font-family: Roboto;
			display: block;
			padding: 15px 0;
			a {
				color: #fff; }
			i {
				color: $primary-color;
				margin-right: 10px; }
			&:hover {
				.cate-more {
					display: block; } } }
		.cate-more {
			display: none;
			background: #3f3937;
			border: none;
			border-radius: 0;
			font-size: 18px;
			font-weight: normal;
			padding: 15px 0;
			top: auto!important;
			bottom: 100%;
			&:before {
				width: 20px;
				height: 100%;
				position: absolute;
				content: '';
				top: 0;
				left: 100%;
				background: #3f3937; }
			a {
				display: block;
				padding-left: 25px;
				padding-right: 35px;
				&:hover {
					color: $primary-color;
					padding-right: 12px;
					i {
						display: initial; } } }
			i {
				display: none; } }
		form {
			display: flex;
			background: #fff;
			padding-left: 20px;
			border-radius: 50px;
			overflow: hidden;
			input {
				height: 45px;
				border: none;
				background: none;
				color: #000;
				flex-grow: 1; }
			button {
				color: #fff;
				width: 40px;
				text-align: center;
				color: $primary-color;
				background: none;
				border: none; } }

		.sign {
			display: flex;
			justify-content: flex-end;
			font-size: 18px;
			text-transform: uppercase;
			a {
				color: #fff; }
			i {
				color: $primary-color;
				margin-left: 10px; } } } }


.home-about-section {
	padding-top: 75px;
	padding-bottom: 100px;
	.title__ {
		h3 {
			text-align: center; }
		p {
			font-weight: 300;
			color: #adadad;
			&._1 {
				text-align: right; } } }
	hr {
		margin-top: 10px;
		margin-bottom: 0; }

	.quick-jump-list {
		margin-top: 60px; }
	.quick-jump {
		display: flex;
		padding: 0 20px;
		> * {
			flex-grow: 1; } } }


.home-sign-section {
	padding-top: 55px;
	padding-bottom: 60px;
	&:before {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba($primary-color, .9); }
	.container {
		position: relative; }

	.text {
		padding-top: 55px; }
	.title__ {
		h3 {
			color: #fff;
			margin-bottom: 0; } }
	hr {
		border-bottom-color: #fff;
		margin-left: 0;
		margin-top: 10px;
		margin-bottom: 20px; }
	p {
		color: #fff;
		letter-spacing: 1px; }
	.button-white {
		margin-top: 33px; }

	hr._2 {
		width: 100%;
		border-bottom-width: 1px;
		border-color: rgba(#fff, .5);
		margin: 50px 0; }

	.img {
		img {
			max-width: 285px;
			margin: 0 auto; } } }


.home-cate-section {
	padding-top: 100px;
	padding-bottom: 80px;
	.title__ {
		margin-bottom: 35px;
		hr {
			margin-left: 0;
			margin-bottom: 0; }
		p {
			color: #adadad;
			text-align: right;
			margin-top: 10px; } }
	.hot-wrap {
		display: flex; }
	.hot {
		flex-grow: 1;
		display: flex;
		position: relative;
		font-family: 'Roboto';
		margin-bottom: 18px;
		.fixed-thumb {
			padding-bottom: 0;
			flex-grow: 1;
			min-height: 200px;
			height: auto; }
		.text {
			position: absolute;
			padding: 25px;
			bottom: 0;
			left: 0;
			width: 100%;
			z-index: 1;
			h3 {

				margin-bottom: 0;
				color: #fff;
				font-weight: 600;
				font-size: 30px;
				letter-spacing: 1px;
				span {
					display: block;
					font-weight: 100; } } }
		.num-tag {
			font-size: 27px;
			color: $dark-text;
			position: absolute;
			top: 25px;
			left: 25px;
			width: 55px;
			height: 55px;
			line-height: 55px;
			text-align: center;
			border-radius: 50%;
			background: #fff; }
		.hot-tag {
			font-size: 18px;
			font-weight: 500;
			color: #fff;
			background: $primary-color;
			padding: 2px 20px;
			border-radius: 50px;
			position: absolute;
			top: 25px;
			right: 25px; } }
	.hot-cate {
		display: flex;
		align-items: center;
		height: 80px;
		box-shadow: 0 5px 25px rgba(#2d2d2d, .1);
		border-right: 5px solid #adadad;
		position: relative;
		margin-bottom: 18px;
		transition: all .3s ease-in-out;
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #fff;
			transition: all .3s ease-in-out; }
		.text {
			display: flex;
			align-items: center;
			padding-left: 28px;
			padding-right: 10px;
			position: relative;
			p {
				font-size: 20px;
				font-weight: 300;
				color: $dark-text;
				margin-bottom: 0;
				display: flex;
				align-items: center;
				span {
					margin-right: 15px;
					display: block;
					width: 40px;
					height: 40px;
					line-height: 40px;
					text-align: center;
					background: #fff;
					border-radius: 30px;
					box-shadow: 0 0 10px rgba(#2d2d2d, .08); } } }
		&:hover {
			border-color: $primary-color;
			&:before {
				opacity: 0; } } } }


@media (max-width: 991.82px) {
	.banner-base-func {
		position: static;
		margin-top: 20px;
		.content {
			border-radius: 5px;
			padding: 20px;
			padding-top: 0;
			flex-wrap: wrap;
			> * {
				width: 50%;
				flex-shrink: 0;
				order: 1; }
			> form {
				width: 100%;
				order: 2; } } }


	.home-about-section {
		.quick-jump>* {
			margin-bottom: 30px; } }

	.home-cate-section .title__ p {
		text-align: left; } }


@media (max-width: 767.82px) {

	.banner-base-func {
		.content {
			>* {
				width: 100%; }
			.sign {
				justify-content: flex-start;
				padding-bottom: 15px; } } }

	.main-banner {
		.fixed-thumb {
			padding-bottom: 80%;
			max-height: 100vh; } }
	.home-about-section .title__ {
		p, p._1 {
			text-align: center; } } }


@media (max-width: 575.82px) {
	.main-banner {
		.fixed-thumb {
			height: 100vh; } }
	.home-about-section {
		padding-top: 35px;
		padding-bottom: 35px;
		.quick-jump-list {
			margin-top: 30px; } }
	.home-sign-section {
		.text {
			padding-top: 0; } }

	.home-cate-section {
		padding-top: 35px;
		padding-bottom: 35px; }

	.home-news-section {
		padding-top: 35px;
		padding-bottom: 35px;
		.title__ {
			margin-bottom: 20px;
			hr {
				margin-bottom: 20px; }
			.button-white {
				margin-left: 0; } } }

	.home-partner-section {
		padding-top: 35px; }

	.home-contact-section {
		padding: 35px 0; } }








